.dropdown--wrap {
  margin-top: 10px;
  text-align: right;
  z-index: 1; }

.select-style {
  display: inline-block;
  position:relative {}
  text-align: left;
  background-color: #f8f8f8;
  position: relative;
  padding: 4px 12px;
  margin-left: 20px;
  margin-bottom: 10px;
  border: 1px solid #CCCCCC;
  border-radius: 2px;
  &:after {
    content: '';
    right: 10px;
    top: 50%;
    margin-top: -3px;
    width: 5px;
    height: 5px;
    background: transparent url(../img/select_arrow.svg) no-repeat;
    background-size: 5px 5px;
    position: absolute;
    pointer-events: none; }


  select {
    text-transform: uppercase;
    color: #333;
    font-weight: 600;
    padding: 3px;
    margin: 0;
    background: #f8f8f8;
    border: none;
    outline: none;
    display: inline-block;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer; }
  /* Targetting Webkit browsers only. FF will show the dropdown arrow with so much padding. */
  @media screen and (-webkit-min-device-pixel-ratio:0) {
    select {
      padding-right: 15px; } } }

