.tooltip {
  display: block !important;
  padding: 4px;
  z-index: 10000;
  margin-bottom: 5px;
  .tooltip-inner {
    background: black;
    color: white;
    border-radius: 2px;
    padding: 5px 10px 4px; }
  .tooltip-arrow {
    display: none; }
  &[aria-hidden='true'] {
    visibility: hidden;
    opacity: 0;
    transition: opacity .15s, visibility .15s; }
  &[aria-hidden='false'] {
    visibility: visible;
    opacity: 1;
    transition: opacity .15s; } }

